#content{padding:50px 0;background-color:#f8f8f8}#content h1{text-align:center;font-size:2rem;margin-bottom:20px;position:relative;font-weight:bold}#content h1::after{content:"";display:block;width:50px;height:3px;background-color:#56397b;margin:10px auto 0}#content .newsCard{padding-top:50px;display:flex;gap:20px;flex-wrap:wrap;justify-content:space-evenly}#content .card{display:flex;flex-direction:column;justify-content:space-between}#content .newsCard div{background-color:#fff;border-radius:10px;box-shadow:0 4px 8px rgba(0,0,0,.1);width:300px;overflow:hidden;position:relative;transition:transform .3s ease}#content .newsCard div:hover{transform:translateY(-10px)}#content .newsCard div img{width:100%;height:200px;object-fit:cover;border-bottom:5px solid #56397b}#content .newsCard div p{padding:15px;font-size:1rem;line-height:1.5;color:#333}#content .newsCard div .meta{display:flex;justify-content:space-between;padding:10px 15px;font-size:.85rem;color:#999}